import Test from "./Test";

import {
  Box,
  Table, TableBody, TableRow, TableCell
} from "@material-ui/core";

// These addresses are owned by Unchained Capital
const OUTPUT_ADDRESSES = {};
OUTPUT_ADDRESSES[MAINNET] = "3DRVz9YUhoXSMgBngvv2JkNReBHvkeJwLs";
OUTPUT_ADDRESSES[TESTNET] = "2NE1LH35XT4YrdnEebk5oKMmRpGiYcUvpNR";

class SignMultisigTransactionTest extends Test {

  FEE_SATS = '10000';

  matches(expected, actual) {
    return JSON.stringify(expected) === JSON.stringify(actual);
  }

  description() {
    return (
      <Box>
        <p>Sign a transaction with inputs from the same 2-of-2 {externalLink(blockExplorerAddressURL(this.params.address, this.params.network), `${this.params.addressType} address`)}.</p>
        <p><small>This transaction is not meant to be broadcast, but just in case, the output address is fixed and owned by Unchained Capital.</small></p>
        <Table>
          <TableBody>

          <TableRow>
            <TableCell>
              Output Address:
            </TableCell>
            <TableCell>
              {externalLink(blockExplorerAddressURL(this.outputAddress(), this.params.network), <code>{this.outputAddress()}</code>)}
            </TableCell>
          </TableRow>

          <TableRow>
            <TableCell>
              Output Amount:
            </TableCell>
            <TableCell>
              {satoshisToBitcoins(this.outputAmountSats()).toString()} BTC
            </TableCell>
          </TableRow>

          <TableRow>
            <TableCell>
              Fees:
            </TableCell>
            <TableCell>
              {satoshisToBitcoins(this.FEE_SATS).toString()} BTC
            </TableCell>
          </TableRow>

          </TableBody>
        </Table>
      </Box>);
  }

  inputs() {
    return this.params.utxos.map((utxo) => ({
      ...utxo,
      ...{
        multisig: generateMultisigFromHex(this.params.network, this.params.addressType, this.params.redeemScript),
        confirmed: true,
      },
    }));
  }

  inputsTotalSats() {
    return this.params.utxos.reduce((total, currentValue) => {return total  + parseInt(currentValue.amountSats);}, 0);
  }

  outputAddress() {
    return OUTPUT_ADDRESSES[this.params.network];
  }

  outputAmountSats() {
    return (this.inputsTotalSats() - parseInt(this.FEE_SATS));
  }

  outputs() {
    return [
      {
        address: this.outputAddress(),
        amountSats: this.outputAmountSats().toString(),
      },
    ];
  }
  
  interaction() {
    return SignMultisigTransaction({
      walletType: this.params.walletType, 
      network: this.params.network,
      inputs: this.inputs(),
      outputs: this.outputs(),
      bip32Paths: this.params.bip32Paths,
    });
  }

  postprocess(result) {
    return (result.signatures ? result.signatures : result);
  }

}

